body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: black;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.mainScreen {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    width: 100vw;
}

/* Intended for settings screen*/
.oneOfTwoCol {
    width: 50vw;
    -webkit-animation: slideLeft 1s ease;
            animation: slideLeft 1s ease;
}

@-webkit-keyframes slideLeft {
    0%   {opacity: 0; -webkit-transform: translateX(100%); transform: translateX(100%)}
    100% {opacity: 1; -webkit-transform: translateX(0%); transform: translateX(0%)}
}

@keyframes slideLeft {
    0%   {opacity: 0; -webkit-transform: translateX(100%); transform: translateX(100%)}
    100% {opacity: 1; -webkit-transform: translateX(0%); transform: translateX(0%)}
}

.modeDisplay {
    border: 3px ridge gold;
    background: rgb(255, 72, 0);
    width: auto;
    padding: 15px;
    border-radius: 50%;
}

.switchModeBtn {
    text-decoration: underline;
}

.switchModeBtn:hover {
    color: red;
}

/* Intended for Noughts and Crosses (Tic-Tac-Toe) */
.ticTacBoard {
    height: 90vmin;
    width: 90vmin;
    display: table;
    border-spacing: 3px;
    -webkit-animation: slideDown 1s ease;
            animation: slideDown 1s ease;
}

@-webkit-keyframes slideDown {
    0%   {opacity: 0; -webkit-transform: translateY(-100%); transform: translateY(-100%)}
    100% {opacity: 1; -webkit-transform: translateY(0%); transform: translateY(0%)}
}

@keyframes slideDown {
    0%   {opacity: 0; -webkit-transform: translateY(-100%); transform: translateY(-100%)}
    100% {opacity: 1; -webkit-transform: translateY(0%); transform: translateY(0%)}
}

.moveAppear {
    -webkit-animation: growText 1s linear;
            animation: growText 1s linear;
}

@-webkit-keyframes growText {
    0%   {font-size: 1vmin}
    100% {font-size: 20vmin}
}

@keyframes growText {
    0%   {font-size: 1vmin}
    100% {font-size: 20vmin}
}

td {
    display: table-cell;
    width: 30vmin;
    vertical-align: middle;
    border: 2px groove gold;
    font-size: 20vmin;
}

.topLeft {
    border-top: none;
    border-left: none;  
}

.topMiddle {
    border-top: none;  
}

.topRight {
    border-top: none;
    border-right: none;
}

.midLeft {
    border-left: none;  
}

.midRight {
    border-right: none;
}

.botLeft {
    border-bottom: none;
    border-left: none;  
}

.botMiddle {
    border-bottom: none;  
}

.botRight {
    border-bottom: none;
    border-right: none;
}

/* Intended for Magic15 screen*/
#magic15Container {
    margin: 1px;
    height: 90vh;
    -webkit-animation: slideRight 1s ease;
            animation: slideRight 1s ease;
}

.freeCardArea {
    position: fixed;
    left: 1vw;
    width: 35vw;    
    min-height: 90vh;
}

.playerArea {
    position: fixed;
    left: 40vw;
    width: 60vw;
    min-height: 90vh;
}

.p1Area {
    height: 44vh;
}

.p2Area {
    height: 44vh;
}

.card {
    background: lightblue;
    color: black;
    border: 2px solid green;
    border-radius: 15%;
    margin: 1vw;
    width: 10vw;
    height: 14vh;
    font-size: 10vw;
    text-align: center;
}

.card-player1 {
    background: lightgreen;
    color: white;
    border: 2px solid black;
    -webkit-animation: cardGrow 0.5s ease;
            animation: cardGrow 0.5s ease;
}
.card-player2 {
    background: lightcoral;
    color: white;
    border: 2px solid black;
    -webkit-animation: cardGrow 0.5s ease;
            animation: cardGrow 0.5s ease;
}

@-webkit-keyframes cardGrow {
    0%   {-webkit-transform: scale(0);transform: scale(0)}
    100% {-webkit-transform: scale(1);transform: scale(1)}
}

@keyframes cardGrow {
    0%   {-webkit-transform: scale(0);transform: scale(0)}
    100% {-webkit-transform: scale(1);transform: scale(1)}
}

.cardPulse {
    -webkit-animation: pulse 1s ease-out;
            animation: pulse 1s ease-out;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

@-webkit-keyframes pulse {
    0%   {opacity: 1; -webkit-transform: scale(1); transform: scale(1)}
    50%  {opacity: 0.5; -webkit-transform: scale(1.1); transform: scale(1.1)}
    100% {opacity: 0.9; -webkit-transform: scale(1); transform: scale(1)} 
}

@keyframes pulse {
    0%   {opacity: 1; -webkit-transform: scale(1); transform: scale(1)}
    50%  {opacity: 0.5; -webkit-transform: scale(1.1); transform: scale(1.1)}
    100% {opacity: 0.9; -webkit-transform: scale(1); transform: scale(1)} 
}
  
@media (orientation: landscape) {
    .freeCardArea {
        position: unset;
        width: auto;   
        min-height: unset;
        height: auto;
    }
    
    .playerArea {
        display: -webkit-flex;
        display: flex;
        position: unset;
        width: auto;
        min-height: auto;
        -webkit-justify-content: space-between;
                justify-content: space-between;
    }
    
    .p1Area {
        display: inline-block;
        height: auto;
        width: 47vw;
    }
    
    .p2Area {
        display: inline-block;
        height: auto;
        width: 47vw;
    }

    .card {
        height: 10vh;
        font-size: 8vh;
    }
}

@-webkit-keyframes slideRight {
    0%   {opacity: 0; -webkit-transform: translateX(-100%); transform: translateX(-100%)}
    100% {opacity: 1; -webkit-transform: translateX(0%); transform: translateX(0%)}
}

@keyframes slideRight {
    0%   {opacity: 0; -webkit-transform: translateX(-100%); transform: translateX(-100%)}
    100% {opacity: 1; -webkit-transform: translateX(0%); transform: translateX(0%)}
}


/* General purpose */
p {
    padding: 1px 10px;
}

.restartBtn {
    background : lightblue;
    border-radius: 15%;
}

/* borders: blue is intended for neutral, red for the side to move*/
.blueBorder {
    border: 1px solid blue;
    border-radius: 5%;
    margin: 2px;
    padding: 5px;
}
  
.redBorder {
    border: 2px solid red;
    border-radius: 5%;
    margin: 2px;
    padding: 5px;
}
.App {
  text-align: center;
  background: black
}

#nav-bar {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  margin: 1px;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.nav-item {
  display: flexbox;
  font-size: 15px;
  margin: 2px 15px 2px 15px;
}

.nav-item:hover {
  background: firebrick;
}

.fancyText {
  -webkit-animation: rollingColours 2s linear;
          animation: rollingColours 2s linear;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

@-webkit-keyframes rollingColours {
  0% {color: red}
  25% {color: silver}
  60% {color: gold}
  100% {color: red}
}

@keyframes rollingColours {
  0% {color: red}
  25% {color: silver}
  60% {color: gold}
  100% {color: red}
}

